import React, { useState, useCallback, useMemo, useEffect } from 'react'
import { History } from 'react-router-dom'

import log from 'loglevel'

import { Loading } from '@northarrowresearch/react-common'
import { UseHostedReturn } from '@northarrowresearch/react-auth'
import { Typography, CircularProgress } from '@material-ui/core'

import {
    useDownloads,
    NotAllowedModal,
    ProjectModal,
    config,
    useProgram,
    useProject,
    googleAnalytics
} from '@riverscapes/react-common'
import ResultsPaneConnect from './SearchResults.connect'
import SearchFormConnect from './SearchForm.connect'
import { appDefaults } from '../config'
import { Program, WarehouseRoleEnum, SearchParams, Project } from '@riverscapes/common'

const url = config.endpoints.graphql as string
/**
 * This is the over-arching wrapper for the app. It combines all the
 * same place. Time will tell if that's a good strategy.
 */
interface Props {
    auth: UseHostedReturn
    history: History
    programs: Program[]
}

const AppConnect: React.FC<Props> = ({ history, auth, programs }: Props) => {
    // This is the project popup.
    const [searchParams, _setSearchParams] = useState<SearchParams>(appDefaults.searchDefaultParams) // maybe context?
    // give access to pendingZips asynchronously
    const setSearchParams = useCallback(
        (val: SearchParams): void => {
            googleAnalytics.event('warehouse', 'search', JSON.stringify(val))
            _setSearchParams(val)
        },
        [_setSearchParams]
    )

    const onProgramChange = useCallback((newProgram: Program) => {
        googleAnalytics.event('warehouse', 'change_program', newProgram.machineName)
        setSearchParams(appDefaults.searchDefaultParams)
    }, [])

    const program = useProgram(programs, onProgramChange)
    const { project, loading: isProjLoading } = useProject(auth, program, true, false)
    const { downloadFileFn, downloadZipFile, pendingZips } = useDownloads(auth, program as Program)
    // const { query, setQuery } = useQueryParams()

    useEffect(() => {
        if (!program && !project) {
            document.title = 'Riverscapes Warehouse'
            googleAnalytics.currentPageView(`warehouse/Program_Chooser`)
        } else if (program && !project) {
            googleAnalytics.currentPageView(`warehouse/${program.name}`)
            document.title = `${program.name} - Riverscapes Warehouse`
        } else if (program && project) {
            googleAnalytics.currentPageView(`warehouse/${project.program}/${project.projType}/${project.id}`)
            document.title = `${project.name} - ${program.name} - Riverscapes Warehouse`
        }
    }, [program, project])

    const navigate: (progName: string, projId?: string) => void = useCallback(
        (progName: string, projId?: string): void => {
            if (projId && projId.length > 10) history.push('/' + progName + '/' + projId)
            else history.push('/' + progName)
        },
        [history]
    )

    const downloadFile = useCallback(
        (proj: Project, fkey: string): void => {
            googleAnalytics.event('warehouse', 'download_file', `${proj.program}/${proj.projType}/${proj.id}/${fkey}`)
            downloadFileFn(proj, fkey)
        },
        [downloadFileFn]
    )
    const downloadZip = useCallback(
        (proj: Project): Promise<void> => {
            googleAnalytics.event('warehouse', 'download_zip', `${proj.program}/${proj.projType}/${proj.id}`)
            return downloadZipFile(proj)
        },
        [downloadZipFile]
    )
    // Modal should be pure and unaffected by anything other than project state
    const projectModal = useMemo(
        () =>
            project && (
                <ProjectModal
                    program={program as Program}
                    project={project}
                    showFileTree
                    onClose={(): void => {
                        if (program) navigate(program.machineName)
                    }}
                    webRaveUrl={appDefaults.webRaveURL}
                    downloadFile={downloadFile}
                    pendingZips={pendingZips}
                    downloadZipFile={downloadZip}
                />
            ),
        [project, downloadFile, downloadZip, navigate, pendingZips, program]
    )

    const hasViewAccess = program && Object.values(WarehouseRoleEnum).indexOf(program.role) > -1
    log.debug('App.Connect', { hasViewAccess, program })
    // The searchConenct should only be affected by search state
    const searchConnect = useMemo(
        () => (
            <React.Fragment>
                <SearchFormConnect
                    {...{
                        getCurrentToken: auth.getCurrentToken,
                        url,
                        program: program as Program,
                        searchParams,
                        setSearchParams
                    }}
                />
                <ResultsPaneConnect
                    {...{
                        getCurrentToken: auth.getCurrentToken,
                        url,
                        navigate,
                        program: program as Program,
                        searchParams,
                        setSearchParams,
                        pendingZips,
                        downloadZipFile: downloadZip
                    }}
                />
            </React.Fragment>
        ),
        [searchParams, program, pendingZips, downloadZip, auth.getCurrentToken, navigate]
    )

    if (!hasViewAccess) return <NotAllowedModal program={program as Program} onClose={(): void => history.push('/')} />

    return (
        <React.Fragment>
            {isProjLoading && (
                <Loading
                    logoSVG={<CircularProgress size="7rem" thickness={7} />}
                    status={
                        <Typography variant="h5" component="div">
                            Loading Project...
                        </Typography>
                    }
                    containerProps={{
                        maxWidth: 'xl',
                        style: {
                            position: 'absolute',
                            zIndex: 1000,
                            top: 0,
                            left: 0,
                            width: '100%',
                            height: '100%',
                            background: '#FFFFFF77'
                        }
                    }}
                />
            )}
            {searchConnect}
            {projectModal}
        </React.Fragment>
    )
}
export default AppConnect
