import React from 'react'
import clsx from 'clsx'
import Typography from '@material-ui/core/Typography'
import ListItem from '@material-ui/core/ListItem'
import Tooltip from '@material-ui/core/Tooltip'
import ListItemAvatar from '@material-ui/core/ListItemAvatar'
import Avatar from '@material-ui/core/Avatar'

import { WarehouseTags, DownloadZipBtn, WebRaveBtn, PendingZips } from '@riverscapes/react-common'
// import log from 'loglevel'
import { makeStyles } from '@material-ui/core/styles'
import { Chip } from '@material-ui/core'
import moment from 'moment'
import { Project, Program } from '@riverscapes/common'
import { appDefaults } from '../../config'

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(2),
        margin: theme.spacing(2)
    },
    itemLeft: {
        flex: '1 1 75%'
    },
    itemMiddle: {
        // border: '1px solid red',
        flex: '1 0 20%',
        display: 'flex',
        textAlign: 'right',
        flexDirection: 'column'
    },
    itemRight: {
        // border: '1px solid red',
        paddingLeft: theme.spacing(1),
        flex: '0 0',
        display: 'flex',
        textAlign: 'right',
        flexDirection: 'column'
    },
    itemRightInner: {
        // border: '1px solid blue',
        display: 'flex',
        textAlign: 'right'
    },
    spacer: {
        flexGrow: 1
    },
    projDate: {},
    itemLine: {
        display: 'flex'
    },
    mm: {
        display: 'flex',
        width: '100%'
    },
    odd: {
        background: theme.palette.background.default
    },
    mmItem: {},
    prodChip: {
        height: theme.spacing(2.3),
        borderRadius: theme.spacing(1) / 2,
        color: '#999',
        border: `1px solid #ddd`,
        background: '#eee',
        marginRight: theme.spacing(1)
    },
    downloadText: {
        // position: 'absolute',
        textAlign: 'center',
        width: '100%'
        // bottom: -theme.spacing(2),
        // right: 0
    }
}))

interface Props {
    project: Project
    odd?: boolean
    onClick: () => void
    program: Program
    pendingZips: PendingZips
    downloadZipFile: (proj: Project) => Promise<void>
}

const SearchResult: React.FC<Props> = ({ project, onClick, program, pendingZips, odd, downloadZipFile }: Props) => {
    const classes = useStyles()
    const pendingSince = pendingZips[project.id]

    // Figure out which fields are mandatory
    const projObj = program.products.find((p) => p.id === project.projType)
    const projObjMand = projObj ? projObj.pathArr.filter((pr) => pr.type === 'Collection') : []

    const pTypeName = projObj ? projObj.name : '<UNKNOWN>'
    const mandatoryMeta = projObjMand.map((po) => ({ key: po.name, val: project.meta[po.name] }), {})

    const lowerMeta: Record<string, string> = Object.keys(project.meta || {}).reduce(
        (acc, key) => ({ ...acc, [key.toLowerCase()]: project.meta[key] }),
        {}
    )

    const pTypeNameVersion = lowerMeta['modelversion'] ? `${pTypeName} v.${lowerMeta['modelversion']}` : pTypeName

    return (
        <ListItem button onClick={onClick} className={clsx({ [classes.odd]: odd as boolean })}>
            <ListItemAvatar>
                <Avatar> {project.projType && project.projType.substr(0, 1)} </Avatar>
            </ListItemAvatar>
            <div className={classes.itemLeft}>
                <Typography variant="body1">{project.name}</Typography>
                <div className={classes.mm}>
                    <Chip className={classes.prodChip} label={pTypeNameVersion} size="small" />
                    {mandatoryMeta.map(({ key, val }, idx) => {
                        const mmFound = program.collections.find((c) => c.name === key)
                        const color = mmFound && mmFound.color ? mmFound.color[500] : 'inherit'
                        return (
                            <Typography
                                key={`mmval-${idx}`}
                                className={classes.mmItem}
                                variant="caption"
                                color="textSecondary"
                            >
                                {idx > 0 && '  '}
                                <span style={{ color }}> {val} </span>
                            </Typography>
                        )
                    })}
                </div>
            </div>
            <div className={classes.itemMiddle}>
                <div>
                    <WarehouseTags tags={project.tags} />
                </div>
                {project.updatedOn && (
                    <Tooltip
                        key={`whTag-dateTooltip`}
                        title={
                            <React.Fragment>
                                <Typography variant="body1">Dates</Typography>
                                <Typography variant="caption">
                                    Created: {moment(project.createdOn, 'x').format('lll')}
                                    <br />
                                    Last updated: {moment(project.updatedOn, 'x').format('lll')}
                                </Typography>
                            </React.Fragment>
                        }
                    >
                        <Typography className={classes.projDate} variant="caption" color="inherit">
                            {moment(project.updatedOn, 'x').format('ll')}
                        </Typography>
                    </Tooltip>
                )}
            </div>
            <div className={classes.itemRight}>
                <DownloadZipBtn {...{ project, pendingSince, downloadZipFile }} btnProps={{ size: 'small' }} />
            </div>
            <div className={classes.itemRight}>
                <WebRaveBtn project={project} url={appDefaults.webRaveURL as string} btnProps={{ size: 'small' }} />
            </div>
        </ListItem>
    )
}

export default SearchResult
