import React, { useState, useEffect, useCallback } from 'react'
import { Route, Switch, History, useHistory } from 'react-router-dom'

import { useHosted, UseHostedReturn } from '@northarrowresearch/react-auth'
import { authCheck, Program, Project, getPrograms } from '@riverscapes/common'
import { ProgramChooser, LoginScreen, config, googleAnalytics, LocalForageCache } from '@riverscapes/react-common'
import { Loading } from '@northarrowresearch/react-common'

import { warehouseIconUrl } from '../config'
import AppConnect from './App.connect'
import AdminComponent from '../components/Admin'
import AppWrapper from '../components/AppWrapper'
import log from 'loglevel'

googleAnalytics.init()

const LOCAL_CACHE = new LocalForageCache('AppAuth', 5 * 60) // 1 hour ttl since programs almost never change
LOCAL_CACHE.prune() // prune once on load

const AppAuthConnect: React.FC = () => {
    const [authInfo, setAuthInfo] = useState<Record<string, unknown>>()
    const [programsState, setProgramsState] = useState<{ programs: Program[]; loading: boolean }>()
    const [isAppPreLoading, setIsAppPreLoading] = useState(true)

    const auth: UseHostedReturn = useHosted(authInfo as Record<string, unknown>, false, config.isDev)
    const history = useHistory()

    // Go get all the startup info we need to start logging in
    // Initial fetch gets us all the program objects
    useEffect(() => {
        authCheck(config.endpoints.graphql as string, '')
            .then((authResp) => {
                log.debug('authResp', authResp)
                const authVals = authResp.data?.auth
                if (authVals && authVals.region) {
                    setAuthInfo({
                        region: authVals.region,
                        userPoolId: authVals.userPool,
                        userPoolWebClientId: authVals.clientId,
                        mandatorySignIn: true,
                        oauth: {
                            domain: authVals.domain,
                            scope: ['email', 'profile', 'openid'],
                            redirectSignIn: config.redirectUrl.toString(),
                            redirectSignOut: config.redirectUrl.toString(),
                            responseType: 'code'
                        }
                    })
                    setIsAppPreLoading(false)
                }
            })
            .catch((err) => {
                log.error('api.getPrograms error', err)
            })
    }, [])

    // This lets us do a redirect after login using localstorage
    useEffect(() => {
        if (!history) return
        const check = async () => {
            const cacheKey = `RSUrl`
            const storageUrl = await LOCAL_CACHE.get<string>(cacheKey)

            const localUrl = history.location.pathname
            log.debug(`[${cacheKey}] Cache:::REDIRECT checking ${storageUrl} === ${localUrl}`)

            if (!auth.isAuthenticated && !auth.error && auth.isAuthComplete) {
                log.debug(`[${cacheKey}] Cache:::REDIRECT:::SET ${localUrl}`)
                LOCAL_CACHE.set(cacheKey, localUrl)
            }
            if (storageUrl && auth.isAuthenticated && auth.isAuthComplete && !auth.error) {
                if (localUrl !== storageUrl) {
                    log.debug(`[${cacheKey}] Cache:::REDIRECT Needed! ${localUrl} !== ${storageUrl}`)
                    history.push(storageUrl)
                }
                // Delete once we're done to stop strange behaviour later
                LOCAL_CACHE.del(cacheKey)
            }
        }
        check()
    }, [history, auth])

    const navigate = useCallback((history: History, program: Program, project?: Project): void => {
        if (project) history.push('/' + program.machineName + '/' + project.id)
        else history.push('/' + program.machineName)
    }, [])

    // Initial fetch gets us all the program objects
    useEffect(() => {
        if (!auth.isAuthenticated || programsState?.programs) return

        setProgramsState({ programs: [], loading: true })
        auth.getCurrentToken()
            .then((jwt) => getPrograms(config.endpoints.graphql as string, jwt))
            .then((allPrograms) => {
                log.debug('API GetPrograms', allPrograms)
                if (allPrograms.data && allPrograms.data.programs) {
                    setProgramsState({ programs: allPrograms.data.programs, loading: false })
                }
            })
            .catch((err) => {
                log.error('api.getPrograms error', err)
                // setIsAppPreLoading(false)
            })
    }, [auth, programsState])

    const logoSVG = <img src={warehouseIconUrl} alt="logo" />

    // render the login helper screen if that's called for
    if (isAppPreLoading) {
        return (
            <AppWrapper>
                <Loading
                    logoSVG={logoSVG}
                    title={'Riverscapes Warehouse'}
                    message={'Loading authentication info'}
                    version={config.version}
                />
            </AppWrapper>
        )
    } else {
        if (!auth.isAuthenticated) {
            if (!auth.isAuthComplete) {
                return (
                    <AppWrapper>
                        <Loading
                            logoSVG={logoSVG}
                            title={'Riverscapes Warehouse'}
                            message={'Authenticating'}
                            version={config.version}
                        />
                    </AppWrapper>
                )
            }
            return (
                <AppWrapper noScroll>
                    <LoginScreen signIn={auth.signIn} title={'Riverscapes Data Warehouse'} />
                </AppWrapper>
            )
        } else if (!programsState || programsState?.loading) {
            return (
                <AppWrapper>
                    <Loading
                        logoSVG={logoSVG}
                        title={'Riverscapes Warehouse'}
                        message={'Loading Programs'}
                        version={config.version}
                    />
                </AppWrapper>
            )
        } else {
            const appWrapProps = { navigate, signOut: auth.signOut, programs: programsState?.programs as Program[] }
            return (
                <Switch>
                    <Route
                        path="/:programId/admin"
                        render={(props): React.ReactElement => (
                            <AppWrapper {...props} {...appWrapProps} noScroll containerProps={{ maxWidth: 'lg' }}>
                                <AdminComponent {...props} programs={programsState?.programs} auth={auth} />
                            </AppWrapper>
                        )}
                    />
                    <Route
                        path="/:programId/:projectId?"
                        render={(props): React.ReactElement => (
                            <AppWrapper {...props} {...appWrapProps}>
                                <AppConnect {...props} auth={auth} programs={programsState?.programs} />
                            </AppWrapper>
                        )}
                    />
                    <Route
                        path="/"
                        render={(props): React.ReactElement => (
                            <AppWrapper {...props} {...appWrapProps} containerProps={{ maxWidth: 'sm' }}>
                                <ProgramChooser {...props} navigate={navigate} programs={programsState?.programs} />
                            </AppWrapper>
                        )}
                    />
                    <Route
                        path="*"
                        render={(props): React.ReactElement => (
                            <AppWrapper {...props} {...appWrapProps}>
                                404 Not found
                            </AppWrapper>
                        )}
                    />
                </Switch>
            )
        }
    }
}
export default AppAuthConnect
