import React, { useMemo } from 'react'
import { ThemeProvider } from '@material-ui/styles'
import { Program } from '@riverscapes/common'
import { Footer, Topbar, themes, config, useProgram } from '@riverscapes/react-common'
import { AppFrame } from '@northarrowresearch/react-common'
import { History, useHistory } from 'react-router-dom'
import { ContainerProps } from '@material-ui/core/Container'
import log from 'loglevel'

interface Props {
    children: React.ReactChild | React.ReactChildren | React.ReactElement
    programs?: Program[]
    history?: History
    noScroll?: boolean
    containerProps?: Partial<ContainerProps>
    signOut?: () => Promise<void>
}

const AppWrapper: React.FC<Props> = ({ children, history, programs, signOut, containerProps, noScroll }: Props) => {
    const program = useProgram(programs)
    return (
        <ThemeProvider theme={themes.home}>
            <AppFrame
                containerProps={{ maxWidth: !program ? 'sm' : 'md', ...containerProps }}
                noScroll={noScroll}
                topbar={
                    <Topbar
                        history={history}
                        programs={programs as Program[]}
                        title="Riverscapes Warehouse"
                        signOutHandler={signOut}
                        provisional
                        shareUrl={window.location.href}
                        program={program as Program}
                    />
                }
                footer={<Footer version={config.version as string} />}
            >
                <>{children}</>
            </AppFrame>
        </ThemeProvider>
    )
}
export default AppWrapper

export const CosmosAppWrapper: React.FC<Props> = ({ children, programs, containerProps, noScroll }: Props) => {
    const signOut = (): Promise<void> => Promise.resolve(log.info('logging out'))
    const history = useHistory()
    const program = useMemo(() => {
        try {
            return programs ? programs[0] : null
        } catch {
            return null
        }
    }, [programs])
    return (
        <ThemeProvider theme={themes.home}>
            <AppFrame
                containerProps={{ maxWidth: !program ? 'sm' : 'md', ...containerProps }}
                noScroll={noScroll}
                topbar={
                    <Topbar
                        history={history}
                        programs={programs as Program[]}
                        title="Riverscapes Warehouse"
                        signOutHandler={signOut}
                        program={program as Program}
                    />
                }
                footer={<Footer version={config.version as string} />}
            >
                <>{children}</>
            </AppFrame>
        </ThemeProvider>
    )
}
